.icon {
  display: block;
  width: $icon-width;
  height: $icon-width;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  margin-right: 0;
  color: $primary-text-color;

  &--menu {
    background-image: url(../images/menu.svg);
    grid-column-end: end;
    transition-duration: $duration;

    &:hover {
      background-image: url(../images/menu-hover.svg);
    }
  }

  &--phone {
    background-image: url(../images/phone.svg);
    grid-column-end: 3;
    text-decoration: none;
    transition-duration: $duration;

    &:hover {
      filter: invert(67%) sepia(16%) saturate(340%)
        hue-rotate(177deg) brightness(99%) contrast(92%);
    }

    @include onDesktop {
      &::before {
        content: "+1 234 5555-55-55";
        position: relative;
        top: 50%;
        transform: translate(-100%, -50%);
        width: max-content;
        display: block;
        padding-right: 8px;
        opacity: 0;
        font-weight: 700;
        font-size: 12px;
        line-height: 16px;
        letter-spacing: 2px;
        pointer-events: none;
      }

      &:hover::before {
        opacity: 1;
        pointer-events: all;
      }
    }
  }

  &--close {
    background-image: url(../images/close.svg);
    grid-column-end: end;
    transition-duration: $duration;

    &:hover {
      background-image: url(../images/close-hover.svg);

    }
  }
}
